"use client";

import { useState } from "react";
import styles from "./HeroSection.module.css";
import ImageCarousel from "@/components/ImageCarousel/ImageCarousel";

export default function HeroSection() {
  const [email, setEmail] = useState("");

  // Exemple d'images - remplacez par vos propres images
  const carouselImages = [
    "/images/image1.jpg",
    "/images/image2.jpg",
    "/images/image3.jpg",
    "/images/image4.jpg",
  ];

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Ici vous pouvez ajouter la logique pour envoyer l'email
    console.log("Email submitted:", email);
    // Réinitialiser le champ après soumission
    setEmail("");
  };

  return (
    <section className={styles.heroSection}>
      <div className={styles.contentWrapper}>
        {/* Carte Newsletter centrée */}
        <div className={styles.newsletterCard}>
          <h2 className={styles.newsletterTitle}>JOIN MY MAILING LIST</h2>
          <p className={styles.newsletterDescription}>
            Be the first to know about my new music releases, and get a free track right now
          </p>
          <form onSubmit={handleSubmit} className={styles.newsletterForm}>
            <input
              type="email"
              placeholder="Your email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className={styles.emailInput}
              required
            />
            <button type="submit" className={styles.signUpButton}>
              SIGN UP
            </button>
          </form>
        </div>

        {/* Carrousel d'images */}
        <ImageCarousel images={carouselImages} autoPlayInterval={4000} />
      </div>
    </section>
  );
}

